.App{
  background: #eee;
  height:180vh;
  width:100vw;
}
.home{
  position:relative;
  display: flex;
  flex-direction: column;
}
.backgroundImage{
  position:absolute;
  top:0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-image: url('./side_image.png');
  background-size: cover; 
  filter:brightness(60%);
  transform: scale(0.7);
}
.heading {
  position: absolute;
  width: 100%;
  text-align: center;
  padding:3%;
}
.search-bar-container{
  padding-top: 50vh;
  align-items: center;
  width: 50%;
  margin: auto;
  min-width: 200px;
  z-index:1;
}

.insights{
  position:relative;
  margin: auto;
  align-items: center;
  top:50vh;
}