.SearchBar {
    background-color: white;
    width:100%;
    padding:0 15px;
    display: flex;
    align-items: center;
    height: 2.5rem;
    border-radius: 10px;
    box-shadow: 0 0 12px #9a9a9a;
}
input{
    height: 100%;
    border:none;
    background-color: transparent;
    font-size: 1.25rem;
    width:100%;
    margin-left: 5px;
}
input:focus{
    outline: none;
}

